import { React, useState, useEffect } from "react"
import { useForm } from "react-hook-form";
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
import {
    Link,
    useParams,
    useHistory,
} from "react-router-dom"
import FormGroup from "../../../Components/Form/FormGroup";
import firebase from '../../../Config/firebase'
import ButtonWithLoading from "../../../Components/Utils/ButtonWithLoading";
/**
 * Formulario de Alta Producto
 * @returns 
 */
const DeleteProductPage = () => {
    const { register, handleSubmit,setValue } = useForm();
    const [loading, setLoading] = useState(false)
    const {id}=useParams()
    let history = useHistory();
    /**
     * 
     * @param {*} data 
     */
    const onSubmit = async (data) => {
        setLoading(true)
        try {
            const document = await firebase.dB.doc("productos/"+id).delete()
            history.push("/admin/products");
            setLoading(false)
        } catch (e) {
            setLoading(false)
            console.log(e);
        }
    }
    /**
     * 
     */
    useEffect(
        ()=>{
             const request = async () =>{
                try{
                    const response = await firebase.dB.doc("productos/"+id)
                    .get()
                    if(response.data()){
                        setValue("title",response.data().title)
                        setValue("price",response.data().price)
                        setValue("category",response.data().category)
                        setValue("description",response.data().description)
                    }
                }catch(e){
                    console.log(e);
                }
                
            }
            request()
            
        },[id,setValue]
    )
    return (
        <div className="col-md-6 mx-auto">
            <div className="card card-body bg-light mt-5">
                <h3>Borrar Producto</h3>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <FormGroup label="Titulo" register={{
                        ...register("title", {
                            disabled:true
                        })
                    }} />
                    <FormGroup select={true} label="Categoria" register={{
                        ...register("category", {
                            disabled:true,
                        })
                    }} options={[{ value: "Vestimenta Hombre", text: "Vestimenta Hombre" },
                    { value: "Vestimenta Mujer", text: "Vestimenta Mujer" },
                    ]} />
                    <FormGroup label="Descripción" register={{
                        ...register("description", {
                            disabled:true
                        })
                    }} />
                    <FormGroup label="Precio" type="number" register={{
                        ...register("price", {
                            disabled:true
                        })
                    }} />
                    <div className="row">
                    <div className="col">
                            <ButtonWithLoading loading={loading} type="submit" variant="success" size="lg"> Borrar</ButtonWithLoading>
                        </div>
                        <div className="col">
                            <LinkContainer to="/admin/products"><Button className="btn-block" variant="danger" size="lg"> Cancelar</Button></LinkContainer>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )

}
export default DeleteProductPage