import {React,useState} from "react"
import { useForm } from "react-hook-form";
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
import {
    Link,
} from "react-router-dom"
import FormGroup from "../../Components/Form/FormGroup";
import AlertDismissible from "../../Components/Utils/AlertDismissible";
import ButtonWithLoading from "../../Components/Utils/ButtonWithLoading";
import firebase from '../../Config/firebase'
/**
 * Formulario de Registro
 * @returns 
 */
const  RegisterPage = () => {
    const { register, handleSubmit, setError, formState: { errors },getValues, reset } = useForm();
    const initialAlert = {
        show:false,
        message: '',
        variant:'success',
        heading: 'Exito'
    }
    const [alert, setAlert] = useState(initialAlert);
    const [loading,setLoading] = useState(false)
    /**
     * 
     * @param {*} data 
     */
    const onSubmit = async (data) => {
        //ocultamos el alert
        setAlert(initialAlert)
        setLoading(true)
        try{
            const responseUser = await firebase.auth.createUserWithEmailAndPassword(data.email,data.password)
            //si tenemos id de usuario creamos la coleccion con los datos del usuario
            if(responseUser.user.uid){
                const document = await firebase.dB.collection("usuarios")
                .add({
                    nombre:data.nombre,
                    apellido:data.apellido,
                    userId:responseUser.user.uid
                })
                console.log("document",document)
                reset({})
                setAlert({
                    ...alert,
                    show:true,
                    message: 'Te Registraste Correctamente.'
                })
                setLoading(false)
            }
        }catch(e){
            setLoading(false)
            switch (e.code) {
                case "auth/email-already-in-use":
                    setError("email", { message: "El Email ya se encuentra Registrado." }, { shouldFocus: true })
                    break;
                case "auth/weak-password":
                    setError("password", { message: "Por favor use una Contraseña más Segura." }, { shouldFocus: true })
                    break;
                default:
                    setAlert({
                        show:true,
                        message: 'Surgio un un error al registrarte. Recargue la pagina e intente de nuevo.',
                        variant:'danger',
                        heading: 'Ups'
                    })
                    break;
            }
    
        }
        
    }
    return (
        <div className="col-md-6 mx-auto">
            <div className="card card-body bg-light mt-5">
                <AlertDismissible  alert={alert}/>
                <h3>Crear Cuenta</h3>
                <p>Ingrese sus Datos</p>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <FormGroup label="Nombre" register={{
                        ...register("nombre", {
                            required: "El Nombre es Obligatorio.",
                            minLength: {
                                value: 3,
                                message: "Debe Ingresar al Menos 3 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Apellido" register={{
                        ...register("apellido", {
                            required: "El Apellido es Obligatorio.",
                            minLength: {
                                value: 3,
                                message: "Debe Ingresar al Menos 3 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Email" type="email" register={{
                        ...register("email", {
                            required: "El Email es Obligatorio.",
                            pattern: {
                                value: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,})+$/,
                                message: "El Email no es Valido."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Contraseña" type="password" register={{
                        ...register("password", {
                            required: "La Contraseña es Obligatoria.",
                            minLength: {
                                value: 8,
                                message: "Debe Ingresar al Menos 8 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Repetir Contraseña" type="password" register={{
                        ...register("passwordConfirm", {
                            required: "Debe Repetir la Contraseña.",
                            minLength: {
                                value: 8,
                                message: "Debe Ingresar al Menos 8 Caracteres."
                            },
                            validate: {
                                matchesPreviousPassword: (value) => {
                                  const { password } = getValues();
                                  return password === value || "Las Contraseña no Coinciden.";
                                }
                            }
                        })
                    }} errors={errors} />
                    <div className="row">
                    <div className="col">
                            <ButtonWithLoading loading={loading} type="submit" variant="success" size="lg"> Registrarme</ButtonWithLoading>
                        </div>
                        <div className="col">
                            <LinkContainer to="/login"><Button className="btn-block" variant="light" size="lg"> Iniciar Sesión</Button></LinkContainer>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )

}
export default RegisterPage