import { React, useState } from "react"
import { useForm } from "react-hook-form";
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
import {
    Link,
} from "react-router-dom"
import FormGroup from "../../Components/Form/FormGroup";
import AlertDismissible from "../../Components/Utils/AlertDismissible";
import ButtonWithLoading from "../../Components/Utils/ButtonWithLoading";
import firebase from '../../Config/firebase';
/**
 * Formulario de Login
 * @returns 
 */
const LoginPage = () => {
    const { register, handleSubmit, setError, formState: { errors }, reset } = useForm();
    const initialAlert = {
        show: false,
        message: '',
        variant: 'success',
        heading: 'Exito'
    }
    const [alert, setAlert] = useState(initialAlert);
    const [loading,setLoading] = useState(false)
    const onSubmit = async (data) => {
        //ocultamos el alert
        setAlert(initialAlert)
        setLoading(true)
        try {
            const responseUser = await firebase.auth.signInWithEmailAndPassword(data.email, data.password)
            //si tenemos id de usuario obtenemos los datos del usuario desde la coleccion
            if (responseUser.user.uid) {
                const userInfo = await firebase.dB.collection("usuarios")
                    //Buscamos en la coleccion donde el id sea igual
                    .where("userId", "==", responseUser.user.uid)
                    .get()
                console.log("userInfo", userInfo.docs[0]?.data())
                setAlert({
                    ...alert,
                    show: true,
                    message: 'Iniciaste Sesión Correctamente.'
                })
                setLoading(false)
                reset({})
            }
        } catch (e) {
            setLoading(false)
            switch (e.code) {
                case "auth/user-not-found":
                    setError("email", { message: "El Email o Contraseña no son Correctos." }, { shouldFocus: true })
                    setError("password", { message: "" }, { shouldFocus: true })
                    break;
                case "auth/wrong-password":
                    setError("email", { message: "El Email o Contraseña no son Correctos." }, { shouldFocus: true })
                    setError("password", { message: "" }, { shouldFocus: true })
                    break;
                default:
                    setAlert({
                        show: true,
                        message: 'Surgio un un error al registrarte. Recargue la pagina e intente de nuevo.',
                        variant: 'danger',
                        heading: 'Ups'
                    })
                    break;
            }
        }
    }
    return (
        <div className="col-md-6 mx-auto">
            <div className="card card-body bg-light mt-5">
                <AlertDismissible alert={alert} />
                <h3>Iniciar Sesión</h3>
                <p>Ingrese su Usuario y Contraseña</p>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <FormGroup label="Email" type="email" register={{
                        ...register("email", {
                            required: "El Email es Obligatorio.",
                            pattern: {
                                value: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,})+$/,
                                message: "El Email no es Valido."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Contraseña" type="password" register={{
                        ...register("password", {
                            required: "La Contraseña es Obligatoria.",
                            minLength: {
                                value: 8,
                                message: "Debe Ingresar al Menos 8 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <div className="row">
                        <div className="col">
                            <ButtonWithLoading loading={loading} type="submit" variant="success" size="lg"> Iniciar Sesión</ButtonWithLoading>
                        </div>
                        <div className="col">
                            <LinkContainer to="/register"><Button className="btn-block" variant="light" size="lg"> Registrarme</Button></LinkContainer>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )
}
export default LoginPage