import { React, useState, useEffect } from "react"
import { getById } from '../../Services/ItemsServices';
import Detail from '../../Components/Checkout/Detail';
import {
    useParams,
} from "react-router-dom"
/**
 * Formulario de CheckoutPage
 * @returns 
 */
const CheckoutPage = () => {
    const { id } = useParams()
    const [producto, setProducto] = useState([])
    useEffect(
        () => {
            getById(id)
                .then(({ data }) => {
                    console.log("data axios details: ", data);
                    setProducto(data)
                }).catch(error => console.log(error))
        }, [id])
    return (
        <div className="row">
            <div className="col-md-8 col-xs-12 col-sm-12">
                <div className="card card-body bg-light mt-5">
                    <h3>Tus Productos</h3>
                    <div className="col-md-12 table-responsive-sm">
                        <table className="table table-hover">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Producto</th>
                                    <th scope="col">Cantidad</th>
                                    <th scope="col">Precio Uni.</th>
                                    <th scope="col">Total</th>
                                </tr>
                            </thead>
                            <tbody>
                                <Detail producto={producto} />
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div className="col-md-4 col-xs-8 col-sm-8">
                <div className="card card-body bg-light mt-5">
                    <h3>Finalizar Compra</h3>
                    <p className="font-weight-bold">Total de la Compra: $ {producto.price}</p>
                    <button className="btn btn-success">
                        Finalizar Compra <span className="spinner-border spinner-border-sm"></span>
                    </button>
                </div>
            </div>
        </div>
    )




}
export default CheckoutPage