import { React, useState } from "react"
import { useForm } from "react-hook-form";
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
import {
    Link,
} from "react-router-dom"
import FormGroup from "../../../Components/Form/FormGroup";
import AlertDismissible from "../../../Components/Utils/AlertDismissible";
import ButtonWithLoading from "../../../Components/Utils/ButtonWithLoading";
import firebase from '../../../Config/firebase'
/**
 * Formulario de Alta Producto
 * @returns 
 */
const CreateProductPage = () => {
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const initialAlert = {
        show: false,
        message: '',
        variant: 'success',
        heading: 'Exito'
    }
    const [alert, setAlert] = useState(initialAlert);
    const [image, setImage] = useState([])
    const [loading, setLoading] = useState(false)
    /**
     * 
     * @param {*} data 
     */
    const onSubmit = async (data) => {
        //ocultamos el alert
        setAlert(initialAlert)
        setLoading(true)
        try {
            const document = await firebase.dB.collection("productos")
                .add({
                    title: data.title,
                    category: data.category,
                    description: data.description,
                    price: data.price,
                })
            if (image) {
                const storageRef = firebase.storage.ref('products/images').child(document.id); // nombre del archivo
                await storageRef.put(image);
                let imageUrl = await storageRef.getDownloadURL()
                const producto = await firebase.dB.doc("productos/" + document.id).get();
                await firebase.dB.doc("productos/" + document.id)
                    .set({
                        ...producto.data(),
                        image: imageUrl
                    })
            }

            reset({})
            setAlert({
                ...alert,
                show: true,
                message: 'El Producto se Guardo Correctamente.'
            })
            setLoading(false)
        } catch (e) {
            setLoading(false)
            console.log(e);
        }
    }
    return (
        <div className="col-md-6 mx-auto">
            <div className="card card-body bg-light mt-5">
                <AlertDismissible alert={alert} />
                <h3>Crear Producto</h3>
                <p>Complete los Datos</p>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <FormGroup label="Imagen" type="file" register={{
                        ...register("image", {
                            onChange: (event) => setImage(event.target.files[0]),
                            validate: {
                                validateSize: files => files[0]?.size < 10000000 || 'Tamaño Maximo 10MB',
                                validateFormats: files =>
                                    ['image/jpeg', 'image/png'].includes(
                                        files[0]?.type
                                    ) || 'Solo se Permiten los Formatos PNG y JPEG',
                            },
                        })
                    }} errors={errors} />
                    <FormGroup label="Titulo" register={{
                        ...register("title", {
                            required: "El Titulo es Obligatorio.",
                            minLength: {
                                value: 3,
                                message: "Debe Ingresar al Menos 3 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup select={true} label="Categoria" register={{
                        ...register("category", {
                            required: "El Apellido es Obligatorio.",
                        })
                    }} options={[{ value: "Vestimenta Hombre", text: "Vestimenta Hombre" },
                    { value: "Vestimenta Mujer", text: "Vestimenta Mujer" },
                    ]} />
                    <FormGroup label="Descripción" register={{
                        ...register("description", {
                            required: "La Descripcion es Obligatoria.",
                            minLength: {
                                value: 3,
                                message: "Debe Ingresar al Menos 3 Caracteres."
                            }
                        })
                    }} errors={errors} />
                    <FormGroup label="Precio" type="number" register={{
                        ...register("price", {
                            required: "El Precio es Obligatoria.",
                            min: {
                                value: 1,
                                message: "Debe Ingresar un Valor Mayor a 0."
                            }
                        })
                    }} errors={errors} />
                    <div className="row">
                        <div className="col">
                            <ButtonWithLoading loading={loading} type="submit" variant="success" size="lg"> Guardar</ButtonWithLoading>
                        </div>
                        <div className="col">
                            <LinkContainer to="/admin/products"><Button className="btn-block" variant="danger" size="lg"> Cancelar</Button></LinkContainer>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )

}
export default CreateProductPage