import { React} from 'react';
import {
    Link
} from "react-router-dom"


/**
 * Componente para mostrar un producto
 * @param {*} param0 
 * @returns 
 */
const Product = ({ producto }) => {
    const styleCardImage = {
            height: "225px", 
            width: "225px", 
            display: "block",
      };
    return (
        
        <>
            <div className="col-md-4 d-flex align-items-stretch">
                <div className="card mb-4 shadow-sm">
                    <img className="card-img-top" alt="Thumbnail [100%x225]" style={styleCardImage} src={producto.data().image}/>
                    <div className="card-body">
                        <h5 className="card-title">{producto.data().title}</h5>
                        <div className="d-flex justify-content-between align-items-center">
                            <div className="">
                                Precio: $ {producto.data().price}
                            </div>
                        </div>
                    </div>
                    <div className="card-footer d-flex justify-content-between align-items-center">
                        <div className="col">
                            <Link className="btn btn-sm btn-outline-info btn-block"to={`/product/detail/${producto.id}`}>Ver Detalle</Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
export default Product;