/**
 * Impostamos la configuracion de axios
 */
import instance from '../Config/axios'

/**
 * Retorna todos los items
 * @param {*} query 
 * @returns 
 */
export const getAll = () => instance.get("products")

/**
 * Retorna el item que coincide con el id
 * @param {*} id 
 * @returns 
 */
export const getById = (id) => instance.get("products/"+id);
