import React from "react"

/**
 * 
 * @param {*} props 
 * @returns form
 */
const FormGroup = (props) => {
    const { label, type, register, placeholder, select, options } = props
    const errors = props.errors ||  false
    return (
        <>
            <div className="form-group">
                {label && <label htmlFor={label}>{label}: <sup>*</sup></label>}
                {select ?
                    <select {...register} className="form-control">
                        {options.map((option,index) => (
                            <option key={index} value={option.value}>{option.text}</option>
                        ))}
                    </select>
                    :
                    <input type={type || "text"} {...register} className={errors[register.name] ? "form-control is-invalid" : "form-control"} placeholder={placeholder || ""} />
                }

                {errors[register.name] &&
                    <span className="invalid-feedback">{errors[register.name].message}</span>
                }
            </div>
        </>
    )
}
export default FormGroup