import { React, useState, useEffect } from "react"
import { set, useForm } from "react-hook-form";
import FormGroup from "../../../Components/Form/FormGroup";
import firebase from '../../../Config/firebase';

import {
    Link,
} from "react-router-dom"
/**
 * Listado de Productos
 * @returns 
 */
const IndexProductsPage = () => {
    const [productos, setProductos] = useState([])
    /**
     * 
     */
    useEffect(
        () => {
            const request = async () => {
                try {
                    const querySnapshot = await firebase.dB.collection("productos")
                        .get()
                    if (querySnapshot.docs) {
                        setProductos(querySnapshot.docs)
                    }
                } catch (error) {
                    console.log(error);
                }
            }
            request()
        }, [])

    return (
        <>
            <div className="row">
                <div className="col-md-4 col-xs-4 col-sm-4 mx-auto">
                    <div className="card card-body bg-light mt-5 text-center">
                        <h3 className="card-title">Productos</h3>
                        <Link to="product/create" className="btn btn-success">Agregar Producto +</Link>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12 col-xs-12 col-sm-12">
                    <div className="card card-body bg-light mt-5">
                        <div className="col-md-12 table-responsive-sm">
                            <table className="table table-hover">
                                <thead  className="thead-dark">
                                    <tr>
                                        <th scope="col">#</th>
                                        <th scope="col">Producto</th>
                                        <th scope="col">Categoria</th>
                                        <th scope="col">Descripción</th>
                                        <th scope="col">Precio</th>
                                        <th scope="col">Acciones</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {productos &&
                                        productos.map((producto, index) => (
                                            <tr key={producto.id}>
                                                <td>{(index + 1)}</td>
                                                <td>{producto.data().title}</td>
                                                <td>{producto.data().category}</td>
                                                <td>{producto.data().description}</td>
                                                <td>{producto.data().price}</td>
                                                <td>
                                                <div className="form-group text-center">
                                                    <Link to={`/admin/product/edit/${producto.id}`} className="btn btn-success btn-sm mr-1">Editar</Link>
                                                    <Link to={`/admin/product/delete/${producto.id}`}  className="btn btn-danger btn-sm">Borrar</Link>
                                                </div>
                                                </td>
                                            </tr>
                                        ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default IndexProductsPage;