import Navbar from './Layout/Navbar/Navbar';
import Footer from './Layout/Footer/Footer';
import HomePage from './Pages/Home/HomePage';
import DetailPage from './Pages/Product/DetailPage';
import IndexProductsPage from './Pages/Product/Admin/IndexProductsPage';
import CreateProductPage from './Pages/Product/Admin/CreateProductPage';
import EditProductPage from './Pages/Product/Admin/EditProductPage';
import DeleteProductPage from './Pages/Product/Admin/DeleteProductPage';
import RegisterPage from './Pages/Auth/RegisterPage';
import LoginPage from './Pages/Auth/LoginPage';
import CheckoutPage from './Pages/Ecommerce/CheckoutPage';
import NotFoundPage from './Pages/Error/NotFoundPage';
import NotFoundProductPage from './Pages/Error/NotFoundProductPage';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom"

function App() {
  return (
    <div className="container-fluid">
      <Router>
        <Navbar />
        <Switch>
          <Route path="/admin/products" exact>
            <IndexProductsPage />
          </Route>
          <Route path="/admin/product/create" exact>
            <CreateProductPage />
          </Route>
          <Route path="/admin/product/edit/:id" exact>
            <EditProductPage/>
          </Route>
          <Route path="/admin/product/delete/:id" exact>
            <DeleteProductPage/>
          </Route>
          <Route path="/register" exact>
            <RegisterPage />
          </Route>
          <Route path="/login" exact>
            <LoginPage />
          </Route>
          <Route path="/product/detail/:id" exact>
            <DetailPage />
          </Route>
          <Route path="/checkout/:id" exact>
            <CheckoutPage />
          </Route>
          <Route path="/" exact>
            <HomePage />
          </Route>
          <Route path="/notfound/product" exact>
            <NotFoundProductPage />
          </Route>
          <Route path="*">
            <NotFoundPage />
          </Route>
        </Switch>
      </Router>
      <Footer />
    </div>
  );
}

export default App;
