import { React} from 'react';
import {
    Link,
} from "react-router-dom"
/**
 * Componente que muestra el detalle de un producto
 * @returns 
 */
const Detail = ({ producto }) => {
    const styleCardImage = {
        height: "300px", 
        width: "300px", 
        display: "block",
  };
    return (
        <>
            <div className="col-md-6 d-flex align-items-stretch">
                <div className="card mb-4 shadow-sm">
                    <img className="card-img-top" alt="Thumbnail [100%x225]" style={styleCardImage} src={producto.data().image} data-holder-rendered="true" />
                    <div className="card-body">
                        <h5 className="card-title">{producto.data().title}</h5>
                        <h6>Categoria: {producto.data().category}</h6>
                        <p className="card-text">Detalle: {producto.data().description}</p>
                        <div className="d-flex justify-content-between align-items-center">
                            <div className="">
                                Precio: $ {producto.data().price}
                            </div>
                        </div>
                    </div>
                    <div className="card-footer d-flex justify-content-between align-items-center">
                        <div className="col">
                            <Link type="button" to={`/checkout/${producto.id}`} className="btn btn-outline-success btn-block">Comprar</Link>
                        </div>

                    </div>
                </div>
            </div>
        </>
    );
}
export default Detail;